import requestHttps from '../../common/request.js';
// import tkiQrcode from '../../components/tki-qrcode/tki-qrcode.vue'
export default {
	// name:'canvas-drawer',
	// components: {
	// 	tkiQrcode
	// },
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			yesshow:false,
			onecheck:'0',
			tk:'',
			list:[],
			goods_id:'',
			prodlist:[],
			imgurl:[],
			scrollLeft:0,
			footshow:false,
			pict_url:'',
			shareImg:'',
			lodding:2
		}
	},
	methods: {
		getmerchan:function(e){
			var that=this
			requestHttps({
				url:'/tb/share/qr',
				method:'POST',
				data:{
					tk_code:that.tk,
					pict_url:that.pict_url
				},
				success:res=>{
					console.log(res)
					that.list=res.data.data.wen_an
					that.shareImg=res.data.data.pic_url 
					that.lodding=1
				}
			})
		},
		getPhotoClickIdx(e) {
			let that = this;
			var list=that.shareImg
			 var imglist = []
			    imglist[0] = that.shareImg
			// let idx = e.currentTarget.dataset.index;
			  
			    uni.previewImage({
			        current:imglist[0],    //  传 Number H5端出现不兼容 
			        urls: imglist
			    }); 
		},
					
		 
		// 
		copycomt:function(){
			var that=this
			var value=that.list
			uni.setClipboardData({
				data: value
			});
		},
		 
		 wxshare:function(){
			 var that=this
			 var texttitl=that.list
				uni.share({
				    provider: "weixin",
				    scene: "WXSceneSession",
				    type: 1,
				    summary: texttitl,
				    success: function (res) {
				        console.log("success:" + JSON.stringify(res));
				    },
				    fail: function (err) {
				        console.log("fail:" + JSON.stringify(err));
				    }
				});
				 
			 
			},
		// 详情
		getdetail:function(e){
			var that=this
			requestHttps({
				url:'/tb/goods/detail',
				method:'POST',
				data:{
					goods_id:that.goods_id
				},
				success:res=>{
					console.log(res.data)
					var data=res.data.data
					that.imgurl=data.small_images
					that.prodlist={
						markprice:data.quanhou_jiage,
						shoppric:data.size,
						coupon:data.coupon_info_money,
						volume:data.volume,
						picname:data.tao_title,
						src:data.pict_url
					}
					console.log(that.prodlist)
					// that.tupshare()
				}
			})
		},
		// 海报
		// tupshare:function(){
		// 	 var that=this
		// 	 console.log(that.prodlist)
		// 	uni.getImageInfo({
		// 		src: that.prodlist.src,
		// 		success(res) {
		// 			console.log(res.path);
		// 			console.log(res);
		// 		}
		// 	})   
		//  },
		 
		tupshare() {
			console.log('111')
		             let that = this;
		             // this.qr_path = path;
		             let system_info = uni.getSystemInfoSync();
		             let ctx = uni.createCanvasContext('firstCanvas');
		             uni.getImageInfo({
		                 src: that.prodlist.src,
		                 success(res) {
		                     console.log(res.path);
							 var path=res.path
		                     ctx.drawImage(res.path, 0, 0, 375, uni.upx2px(1020));
		                     let linearGrad = ctx.createLinearGradient(0, 0, 800, 0);
		                     linearGrad.addColorStop('0.25', '#8b00d2');
		                     linearGrad.addColorStop('0.5', '#003fb3');
		                     linearGrad.addColorStop('0.75', '#ff3ef0');
		                     ctx.fillStyle = '#FFFFFF';
		                     ctx.fillRect(uni.upx2px(500), uni.upx2px(790), uni.upx2px(200), uni.upx2px(210));
		                     ctx.drawImage(path, uni.upx2px(520), uni.upx2px(800), uni.upx2px(160), uni.upx2px(160));
		                     ctx.font = '13px Arial';
		                     ctx.fillStyle = '#000';
		                     ctx.fillText('长按保存二维码', uni.upx2px(508), uni.upx2px(990));
		                     ctx.draw(false, () => {
		                         uni.canvasToTempFilePath({
		                             x: 0,
		                             y: 0,
		                             width: 375,
		                             height: uni.upx2px(1020),
		                             destWidth: 375,
		                             destHeight: uni.upx2px(1020),
		                             canvasId: 'firstCanvas',
		                             success: function(res) {
		                                 uni.saveImageToPhotosAlbum({
		                                     filePath: res.tempFilePath,
		                                     success: function() {
		                                         console.log('save success');
		                                     }
		                                 });
		                             },
		                             fail(e) {
		                                 console.log(e);
		                                 uni.showToast({
		                                     title: '生成海报失败',
		                                     icon: 'none'
		                                 });
		                             }
		                         });
		                     });
		                 },
		                 fail(error) {
		                     console.log(error);
		                 }
		             });
		             
		 
		             
		         },
		         saveToAlbum() {
		             this.$refs.qrcode._makeCode();
		         },
		 
		 
		 // 选中
		changeCheckbox:function(e){
			console.log(e)
		},
		
		// 返回上个页面
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
		// 保存到图库
		saveQrcode() {
			var that=this
			// this.$refs.qrcode._saveCode()
			uni.saveImageToPhotosAlbum({
				filePath: that.shareImg,
				success: function () {
					uni.showToast({
						title: '图片保存成功',
						icon: 'success',
						duration: 2000
					});
				}
			});
		},
	},
	onLoad:function(option){
		var that=this
		console.log(option)
		that.goods_id=option.goods_id
		that.tk=option.tk
		that.pict_url=option.pict_url
		that.getmerchan()
		that.getdetail()
		// 
		
		
		
	}
}
